body[data-layout-mode="dark"] {
    .apexcharts-gridline {
        stroke: lighten($gray-dark-200, 2.5%);
    }

    .apexcharts-tooltip.apexcharts-theme-light {
        background-color: $gray-dark-200;
        border-color: lighten($gray-dark-200, 2.5%);

        .apexcharts-tooltip-title {
            background-color: $gray-dark-300;
            border-color: lighten($gray-dark-300, 2.5%);
        }
    }

    .apexcharts-yaxis-texts-g.apexcharts-xaxis-inversed-texts-g,
    .apexcharts-grid-row {
        + line {
            stroke: lighten($gray-dark-300, 2.5%);
        }
    }

    .apexcharts-xaxis line,
    .apexcharts-treemap-rect,
    .apexcharts-heatmap-rect,
    .apexcharts-pie-area {
        stroke: lighten($gray-dark-300, 2.5%);
    }

    .apexcharts-radialbar-track.apexcharts-track {
        path {
            stroke: lighten($gray-dark-300, 2.5%);
        }
    }
    .apexcharts-radar-series.apexcharts-plot-series {
        polygon,
        line {
            fill: $gray-dark-300;
            stroke: lighten($gray-dark-300, 2.5%);
        }
    }
    .apexcharts-pie {
        circle {
            stroke: lighten($gray-dark-300, 2.5%);
        }
    }

    // SweetAlert

    .swal2-popup {
        background-color: $gray-dark-300 !important;
    }

    .swal2-content {
        color: $gray-dark-500;
    }

    // Range slider

    .irs--square {
        .irs-max,
        .irs-min {
            background: $gray-dark-300;
            color: $gray-dark-400;
        }

        .irs-line,
        .irs-handle {
            background-color: $gray-dark-300 !important;
        }

        .irs-grid-text {
            color: $gray-dark-400;
        }
    }

    // toast
    .toast {
        background-color: rgba($gray-dark-300, 0.85);
        border: 1px solid rgba($white, 0.1);
    }
    .toast-header {
        background-color: rgba($gray-dark-300, 0.85);
        color: $gray-dark-400;
    }

    // Select2

    .select2-container {
        .select2-selection {
            background-color: lighten($gray-dark-200, 2.5%);
            border: 1px solid $gray-dark-300 !important;
            .select2-selection__rendered {
                color: $gray-dark-500;
            }
            .select2-search__field {
                color: $gray-dark-500;
                &::placeholder {
                    color: $gray-dark-400;
                }
            }
            .select2-selection__placeholder {
                color: $gray-dark-400;
            }
            .select2-selection__choice {
                background-color: lighten($gray-dark-300, 2.5%);
                border: 1px solid $gray-dark-300;
            }
        }

        .select2-results__option[aria-selected="true"] {
            background-color: $gray-dark-300;
            color: $gray-dark-500;
        }
        .select2-dropdown {
            background-color: lighten($gray-dark-200, 2.5%);
            border: 1px solid $gray-dark-300;
        }
        .select2-search--dropdown {
            background-color: lighten($gray-dark-200, 2.5%);
            .select2-search__field {
                background-color: lighten($gray-dark-200, 2.5%);
                border: 1px solid $gray-dark-300;
                color: $gray-dark-500;
            }
        }
    }

    // color picker
    .sp-container {
        background-color: lighten($gray-dark-200, 2.5%);
        button {
            color: $gray-dark-500;
            &.sp-choose {
                color: $white;
            }
            &.sp-palette-toggle {
                background-color: $gray-dark-300;
            }
        }
    }
    .sp-palette-container {
        border-right: 1px solid $gray-dark-300;
    }

    // datepicker

    .datepicker {
        border-color: $gray-dark-300;
        table {
            tr {
                td {
                    span,
                    &.day {
                        &.focused,
                        &:hover {
                            background-color: $gray-dark-300;
                        }
                    }

                    &.range {
                        background-color: $gray-dark-300;
                        &.disabled,
                        &.disabled:hover,
                        &:hover {
                            background-color: $gray-dark-300;
                        }
                    }
                }
            }
        }

        .datepicker-switch,
        .next,
        .prev,
        tfoot tr th {
            &:hover {
                background-color: $gray-dark-300;
            }
        }
    }

    // dropzone
    .dropzone {
        background-color: lighten($gray-dark-200, 2.5%);
        border-color: $gray-dark-300;
    }

    // Wizard

    .wizard {
        .steps > ul > li {
            a {
                color: $gray-dark-500 !important;
            }
        }
    }

    // datatable

    .activate-select .sorting_1 {
        background-color: $gray-dark-200;
    }

    .dt-button-collection {
        .dt-button {
            &.active {
                background-color: $gray-dark-300;
            }
        }
    }

    // Google Maps
    .gmaps,
    .gmaps-panaroma {
        background: $gray-dark-300;
    }

    // Form editors.scss

    .ql-container {
        &.ql-snow {
            border-color: lighten($gray-dark-300, 2%);
        }
    }

    .ql-bubble {
        border: lighten($gray-dark-300, 2%) solid lighten($gray-dark-300, 2%) !important;
        border-radius: $input-border-radius;
    }

    .ql-toolbar {
        font-family: $font-family-base !important;
        span {
            color: $gray-dark-500;

            &:hover {
                color: $primary !important;
            }
        }

        &.ql-snow {
            border-color: lighten($gray-dark-300, 2%) !important;

            .ql-picker.ql-expanded {
                .ql-picker-label {
                    border-color: transparent;
                }
            }
        }
    }

    .ql-snow {
        .ql-stroke,
        .ql-script,
        .ql-strike svg {
            stroke: $gray-dark-500;
        }
        .ql-fill {
            fill: $gray-dark-500;
        }
    }

    .ql-snow {
        .ql-picker-options {
            background-color: $gray-dark-200;
            border-color: lighten($gray-dark-300, 2%) !important;
        }
    }

    // flatpickr

    .flatpickr-calendar {
        background-color: lighten($gray-dark-200, 2.5%) !important;
        box-shadow: 1px 0 0 $gray-dark-300, -1px 0 0 $gray-dark-300, 0 1px 0 $gray-dark-300, 0 -1px 0 $gray-dark-300,
            0 3px 13px rgba(0, 0, 0, 0.08) !important;
        &.hasTime {
            .flatpickr-time {
                border-top: 1px solid $gray-dark-300 !important;
            }
        }
    }

    .flatpickr-weekdays {
        border-color: $gray-dark-300;
    }

    .flatpickr-day {
        color: $gray-dark-500 !important;
        &:hover,
        &:focus {
            background-color: rgba($gray-dark-300, 0.7) !important;
        }

        &.today {
            &:hover,
            &:focus {
                color: $gray-dark-500 !important;
            }
        }

        &.inRange,
        &.prevMonthDay.inRange,
        &.nextMonthDay.inRange,
        &.today.inRange,
        &.prevMonthDay.today.inRange,
        &.nextMonthDay.today.inRange,
        &:hover,
        &.prevMonthDay:hover,
        &.nextMonthDay:hover,
        &:focus,
        &.prevMonthDay:focus,
        &.nextMonthDay:focus {
            background-color: $gray-dark-300;
            border-color: $gray-dark-300;
        }

        &.selected {
            background-color: $primary !important;
            border-color: $primary !important;
            color: $white !important;
        }

        &.flatpickr-disabled,
        &.flatpickr-disabled:hover,
        &.prevMonthDay,
        &.nextMonthDay,
        &.notAllowed,
        &.notAllowed.prevMonthDay,
        &.notAllowed.nextMonthDay {
            color: rgba($gray-dark-500, 0.3) !important;
        }

        &.inRange {
            box-shadow: -5px 0 0 $gray-dark-300, 5px 0 0 $gray-dark-300;
        }
    }

    .flatpickr-time {
        input,
        .flatpickr-time-separator,
        .flatpickr-am-pm {
            color: $gray-dark-500 !important;
        }

        .numInputWrapper span {
            &.arrowUp:after {
                border-bottom-color: $gray-dark-500;
            }
            &.arrowDown:after {
                border-top-color: $gray-dark-500;
            }
        }
    }
}
